import * as React from 'react'
import { Link, graphql } from 'gatsby'

import Bio from '../components/Bio'
import Layout from '../components/Layout'
import Seo from '../components/Seo'
import { rhythm, scale } from '../utils/typography'

const BlogPostTemplate = ({ data, location, pageContext }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata?.title || 'Title'
  const { previous, next } = pageContext

  return (
    <Layout location={location} title={siteTitle}>
      <article itemScope itemType="http://schema.org/Article">
        <header style={{ textAlign: 'center', marginBottom: rhythm(1.5) }}>
          {/* Decorative element above title */}
          <div
            style={{
              color: '#8b4513',
              fontSize: '1.5rem',
              marginBottom: rhythm(0.5),
              opacity: 0.6,
            }}
          >
            ❧
          </div>
          <h1
            style={{
              fontFamily: "'Playfair Display', Georgia, serif",
              marginTop: 0,
              marginBottom: rhythm(0.5),
              lineHeight: 1.2,
              fontWeight: 900,
            }}
            itemProp="headline"
          >
            {post.frontmatter.title}
          </h1>
          <p
            style={{
              ...scale(-1 / 5),
              display: 'block',
              marginBottom: rhythm(0.5),
              fontFamily: "'Libre Baskerville', Georgia, serif",
              fontStyle: 'italic',
              color: '#666',
            }}
          >
            Published {post.frontmatter.date}
          </p>
          {post.frontmatter.tags && post.frontmatter.tags.length > 0 && (
            <div style={{ marginBottom: rhythm(0.5) }}>
              {post.frontmatter.tags.map(tag => (
                <Link
                  key={tag}
                  to={`/tags/${tag.toLowerCase().replace(/\s+/g, '-')}/`}
                  style={{
                    display: 'inline-block',
                    backgroundColor: 'transparent',
                    color: '#8b4513',
                    padding: '0.15rem 0.5rem',
                    marginRight: '0.4rem',
                    marginBottom: '0.4rem',
                    border: '1px solid rgba(139, 69, 19, 0.3)',
                    fontSize: '0.75rem',
                    textDecoration: 'none',
                    boxShadow: 'none',
                    fontFamily: "'Libre Baskerville', Georgia, serif",
                    fontStyle: 'italic',
                    transition: 'all 0.2s ease',
                  }}
                >
                  {tag}
                </Link>
              ))}
            </div>
          )}
          {/* Decorative divider */}
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              marginTop: rhythm(1),
            }}
          >
            <span
              style={{
                flex: '0 0 80px',
                height: '1px',
                background: 'linear-gradient(to right, transparent, #8b4513)',
              }}
            />
            <span
              style={{
                margin: '0 1rem',
                color: '#8b4513',
                fontSize: '0.8rem',
              }}
            >
              ✦
            </span>
            <span
              style={{
                flex: '0 0 80px',
                height: '1px',
                background: 'linear-gradient(to left, transparent, #8b4513)',
              }}
            />
          </div>
        </header>
        <section
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp="articleBody"
          style={{
            lineHeight: 1.8,
          }}
        />
        <hr
          style={{
            marginTop: rhythm(2),
            marginBottom: rhythm(1),
            border: 'none',
            height: '2px',
            background: 'linear-gradient(to right, transparent, #8b4513, transparent)',
          }}
        />
        <footer>
          <Bio />
        </footer>
      </article>
      <nav
        style={{
          marginTop: rhythm(1),
          padding: rhythm(0.75),
          background: 'rgba(139, 69, 19, 0.03)',
          border: '1px solid rgba(139, 69, 19, 0.1)',
        }}
      >
        <ul
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            listStyle: 'none',
            padding: 0,
            margin: 0,
          }}
        >
          <li style={{ maxWidth: '45%' }}>
            {previous && (
              <Link
                to={previous.fields.slug}
                rel="prev"
                style={{
                  fontFamily: "'Libre Baskerville', Georgia, serif",
                  fontSize: '0.85rem',
                  color: '#8b4513',
                  borderBottom: 'none',
                }}
              >
                <span style={{ opacity: 0.6, marginRight: '0.3rem' }}>←</span>
                <span style={{ fontStyle: 'italic' }}>Prior:</span>{' '}
                {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li style={{ maxWidth: '45%', textAlign: 'right' }}>
            {next && (
              <Link
                to={next.fields.slug}
                rel="next"
                style={{
                  fontFamily: "'Libre Baskerville', Georgia, serif",
                  fontSize: '0.85rem',
                  color: '#8b4513',
                  borderBottom: 'none',
                }}
              >
                <span style={{ fontStyle: 'italic' }}>Next:</span>{' '}
                {next.frontmatter.title}
                <span style={{ opacity: 0.6, marginLeft: '0.3rem' }}>→</span>
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  )
}

export const Head = ({ data }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata?.title
  return <Seo title={`${post.frontmatter.title} | ${siteTitle}`} description={post.excerpt} />
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        tags
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
